import React from "react";
import { DialogOverlay, DialogContent } from "@reach/dialog";
import { useSpring, animated } from "react-spring";
import "./styles.css";
import SidebarUserProfile from "./components/sidebarUserProfile/SidebarUserProfile";

function useDelayClose(swip) {
  const [delayClose, setDelayClose] = React.useState(false);
  const [everOpen, setEverOpen] = React.useState(false);
  React.useEffect(() => {
    if (!swip) {
      setDelayClose(true);
      const timeout = setTimeout(() => {
        setDelayClose(false);
      }, 200);
      return () => clearTimeout(timeout);
    } else {
      setEverOpen(true);
      setDelayClose(true);
    }
  }, [swip]);
  return delayClose && everOpen;
}

export default function Sidebar(props) {
  const { right } = useSpring({ right: props.swip ? 0 : -300 });
  const delayClose = useDelayClose(props.swip);
  return (
    <DialogOverlay
      isOpen={props.swip || delayClose}
      onDismiss={props.onDismiss}
    >
      <DialogContent
        aria-label="Sidebar"
        style={{
          width: 0,
          height: 0,
          padding: 0,
          margin: 0,
        }}
      >
        <animated.div
          style={{
            right: right,
            position: "fixed",
            width: 300,
            top: 0,
            bottom: 0,
            padding: 0,
            margin: 0,
            backgroundColor: "#9385e0",
            zIndex: 100,
          }}
        >
          <div className="flex flex-wrap">
            <div className="user-profile-sidebar w-full">
              <SidebarUserProfile />
            </div>
          </div>
        </animated.div>
      </DialogContent>
    </DialogOverlay>
  );
}
